
import { defineComponent, h } from "vue";
import CopyCard from "@/components/copy/CopyCard.vue";
import SecretCopyCard from "@/components/copy/SecretCopyCard.vue";

export default defineComponent({
  props: {
    item: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    return () => {
      const withPrefix =
        typeof props.item === "string" && props.item.includes("#");

      if (!withPrefix) {
        return props.item;
      }

      const [prefix, value] = props.item.split("#", 2);

      switch (prefix) {
        case "copy": {
          return h(CopyCard, { value }, value);
        }
        case "secretCopy": {
          return h(SecretCopyCard, { value }, value);
        }
        default: {
          return props.item;
        }
      }
    };
  },
});
