import { defineStore } from "pinia";
import { useRouter } from "vue-router";
import { ref } from "vue";
import { useToast } from "vue-toastification";

import { CreateEcommTerminalRequest } from "../model/ecomm.request";
import {
  createEcommTerminal as sendCreateEcommTerminalRequest,
  editEcommTerminal as sendEditEcommTerminalRequest,
  getEcommTerminalById,
  getEcommTerminals as sendGetEcommTerminalsRequest,
} from "../api/ecomm.api";
import { EcommTerminal } from "../model/ecomm-terminal";

import {
  mapEcommTerminalFromReponse,
  mapEcommTerminalToRequest,
  mapTerminalsFilterToRequest,
} from "../api/ecomm.mapper";

const toast = useToast();
const router = useRouter();

export const useEcommTerminalStore = defineStore("ecomm-terminal", () => {
  const isFormLoading = ref(false);
  const isFetching = ref(false);
  const terminals = ref<EcommTerminal[]>([]);
  const terminal = ref<EcommTerminal | null>(null);
  const total = ref(0);

  async function createEcommTerminal(payload: CreateEcommTerminalRequest) {
    try {
      isFormLoading.value = true;
      const normalizedPayload = mapEcommTerminalToRequest(payload);
      await sendCreateEcommTerminalRequest(normalizedPayload);
      toast.success("Термінал успішно створено");
    } catch (error) {
      console.log(error);
      toast.error("Помилка при створенні терміналу");
    } finally {
      isFormLoading.value = false;
    }
  }

  async function editEcommTerminal(
    id: number,
    payload: CreateEcommTerminalRequest
  ) {
    try {
      isFormLoading.value = true;
      const normalizedPayload = mapEcommTerminalToRequest(payload);
      await sendEditEcommTerminalRequest(id, normalizedPayload);
      toast.success("Термінал успішно змінено");
      router.back();
    } catch (error) {
      toast.error("Помилка при редагуванні терміналу");
    } finally {
      isFormLoading.value = false;
    }
  }

  async function getEcommTerminals(filters?: any) {
    try {
      isFetching.value = true;
      const response = await sendGetEcommTerminalsRequest(
        mapTerminalsFilterToRequest(filters)
      );

      const normilizedTerminals = (response.list as EcommTerminal[]).map(
        mapEcommTerminalFromReponse
      );

      terminals.value = normilizedTerminals;

      total.value = response.totalCount;
    } catch (e) {
      console.error(e);
    } finally {
      isFetching.value = false;
    }
  }

  async function getEcommTerminal(id: string) {
    console.log("getEcommTerminal");
    isFetching.value = true;
    const response = await getEcommTerminalById(id);

    terminal.value = mapEcommTerminalFromReponse(response);
    isFetching.value = false;
  }

  return {
    isFormLoading,
    isFetching,
    terminals,
    terminal,
    total,
    createEcommTerminal,
    editEcommTerminal,
    getEcommTerminals,
    getEcommTerminal,
  };
});
